const getImgUrl = img => {
	let locationLink = window.location.href;
	let preResult = locationLink.slice(locationLink.indexOf('app-assets/') + 'app-assets/'.length);

	if (typeof img === 'undefined') {
		throw new Error('Img url must be called with argument');
	}

	if (process.env.NODE_ENV === 'production') {
		return `/app-assets/${preResult.slice(0, preResult.indexOf('/'))}/app/static/images/${img}`;
	}

	return `/iframe/static/images/${img}`;
}

const msgCall = (typeCall, name, phone) => {
	if (typeCall === 'incoming') {
		return `${name} is calling from number ${phone}`;
	}

	return `alling to ${name} by number ${phone}`;
}

export { getImgUrl, msgCall };
