import Vue from 'vue'
import App from './contactInfo.vue'
import 'statics/libs/Fresh_client.js'
/* eslint-disable no-new */
const init = function() {
	try {
		const client = window._app.getClient();
		console.log('CLIENT');
		console.log(client);
		new Vue({
			el: '#wildix-contact-modal',
			render: h => h(App, { props: { clientProps: client.client } })
		})
	} catch (e) {
		console.error(e);
	}
}
// $(document).ready(init)

document.addEventListener('DOMContentLoaded', init);
