<template>
<div class="fw-widget-wrapper">
	<div class="contactInfo" v-if="contact !== null">
		<div v-if="contact.phone !== null">
			<div class="title">Work Phone</div>
			<div class="action">
				<div v-on:click.prevent="call(contact.phone)">
					<img class="wildixLogo" :src="imgUrl" alt="Wildix Logo">
					<a href="#">{{contact.phone}}</a>
				</div>
				<button type="button" class="btn-primary btn-sm btnCall" v-on:click="call(contact.phone)">
					<span class="glyphicon glyphicon-earphone iconBtnCall" aria-hidden="true"></span>
					<span>Call</span>
				</button>
			</div>
		</div>
		<div v-if="contact.mobile !== null">
			<div class="title">Mobile Phone</div>
			<div class="action">
				<div v-on:click.prevent="call(contact.mobile)">
					<img class="wildixLogo" :src="imgUrl" alt="Wildix Logo">
					<a href="#">{{contact.mobile}}</a>
				</div>
				<button type="button" class="btn-primary btn-sm btnCall" v-on:click="call(contact.mobile)">
					<span class="glyphicon glyphicon-earphone iconBtnCall" aria-hidden="true"></span>
					<span>Call</span>
				</button>
			</div>
		</div>
		<div v-if="contact.phone === null && contact.mobile === null">
			<div class="emptyData">
				<img class="wildixLogo" :src="imgUrl" alt="Wildix Logo">
				<div class="title">No phone number</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>
import { getImgUrl } from '@/utils/helper'

export default {
	props: {
		clientProps: {
			type: Object,
			default: () => { return null },
			required: true
		}
	},

	data() {
		return {
			contact: null,
			client: this.clientProps,
			globalSidebar: null
		};
	},

	computed: {
		imgUrl() {
			return getImgUrl('w.png');
		}
	},

	methods: {
		async call(phone) {
			if (this.globalSidebar == null) {
				try {
					await this.findGlobalPopup();
					this.call(phone);
					return false;

				} catch (e) {
					console.error(e);
				}
			}

			if (phone && this.globalSidebar !== null) {
				window._app.instanceSend({
					message: { type: 'call', phone: phone },
					receiver: [this.globalSidebar.instance_id]
				})

				return false;
			}
		},
		async findGlobalPopup() {
			const allInstances = await window._app.instanceGet();

			this.globalSidebar = allInstances.find(x => x.location === 'cti_global_sidebar') || null;
		},
		getTicketContact(client) {
			return new Promise(async (resolve, reject) => {
				try {
					const res = await window._app.getClientData('contact');s
					resolve(res.contact);

				} catch (e) {
					reject(new Error(e));
				}
			}).then(v => v)
		},
		registerAppEvent(client) {
			window._app.registerAppEvent('app.activated', this.appActivated, this);
			window._app.registerAppEvent('app.deactivated', this.appDeactivated, this);
		},
		async appActivated(client) {
			try {
				this.contact = await this.getTicketContact(client) || null;
			} catch (e) {
				console.error(e);
			}
		},
		appDeactivated() {
			// console.log('App deactivated')
		}
	},

	async mounted() {
		try {
			this.contact = await this.getTicketContact(this.client) || null;
			await this.findGlobalPopup();
			this.registerAppEvent(this.client);

		} catch (e) {
			console.error(e);
		}
	}
}
</script>

